@import url(https://fonts.googleapis.com/css2?family=Newsreader&display=swap);
* {
    font-family: 'Newsreader', serif;
}

body {
    overflow-x: hidden;
}

body,
div {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
}

li {
    margin-top: 10px;
}
.section1 {
    width: 100vw;
    max-width: 100%;
    height: 600px;
    position: relative;
    z-index: 9;

    box-shadow: 0 5px 22px 10px black;
    overflow: hidden;
}

.section2 {
    padding: 40px 30px 0 30px;
    width: 100vw;
    max-width: 100%;
    height: 1200px;
    position: relative;
    display: flex;
    flex-wrap: wrap;

    background-repeat: no-repeat;
    background-size: cover;
}

.section3 {
    width: 100vw;
    max-width: 100%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    position: absolute;
    z-index: 99;

    font-size: 20px;

    background-color: #676b89;
}

/* ********* Section 1 Children *********** */
.section1 .background_image {
    width: 100%;
    max-width: 3840px;
    min-width: 1000px;
    position: absolute;
    bottom: 0;
}

.greeting {
    margin: 10px 0 0 10px;
    padding: 10px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    position: relative;
    z-index: 99;

    font-size: 150px;
    color: white;

    background-color: rgba(0, 0, 0, 0.3);
    box-shadow: 0 0 20px 2px black;
    -webkit-filter: brightness(200%);
            filter: brightness(200%);
}

.gallery {
    width: 40vw;
    min-width: 400px;
    height: 25vw;
    min-height: 250px;
    position: absolute;
    z-index: 999;
    top: 30%;
    right: 4%;

    box-shadow: 0 0 20px 2px black;
}


/* ********* Section 2 Children *********** */
.spoons {
    width: 100%;
    max-width: 3840px;
    height: auto;
    position: absolute;
    bottom: 0;
    left: 0;
    pointer-events: none;
}

.left_side {
    padding: 0 0 0 70px;
    width: 50%;
    height: 50%;
    display: flex;
    align-items: center;

    color: #1e1e1e;
    font-size: 110px;
    -webkit-filter: contrast(200%);
            filter: contrast(200%);
}

.right_side {
    width: 50%;
    height: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.right_side img {
    width: 70%;
}

.call_to_action {
    margin: -22% 0 0 0;
    width: 100%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;

    text-align: center;
    color: #1e1e1e;
    font-size: 70px;
    -webkit-filter: contrast(200%);
            filter: contrast(200%);
}

.call_to_action a {
    text-decoration: none;
    color: unset;
}


/* ********* Section 3 Children *********** */
.contacts {
    padding: 0 0 30px 0;
    width: 100%;
    height: 30%;
    display: flex;
    justify-content: flex-start;

    background-color: #303443;
    box-shadow: 0 0 5px 0 black;
}

.contact_section {
    margin: 0 20px;
    width: 200px;
    height: 100%;

    color: white;
    font-size: 22px;
}

/* ********* Responsive Code *********** */

@media only screen and (max-width: 948px) {
    .section1 .greeting {
        font-size: 110px;
    }
}

@media only screen and (max-width: 768px) {

    /* ***************** Section 1 children ************* */
    .section1 .greeting {
        font-size: 80px;
    }

    /* ***************** Section 2 children ************* */
    .section2 {
        height: 120vh;
    }

    .left_side {
        padding-left: unset;
        font-size: 80px;
    }

    .call_to_action {
        font-size: 50px;
    }
}

@media only screen and (max-width: 501px) {

    /* ***************** Section 1 children ************* */
    .section1 {
        height: 45vh;
    }

    .section1 .greeting {
        font-size: 60px;
    }

    .section1 .background_image {
        height: unset;
        width: 100%;
        min-width: 500px;
    }

    .gallery {
        margin-top: 20px;
        width: 90vw;
        min-width: unset;
        height: 56.25vw;
        min-height: unset;
        top: unset;
        right: unset;
        left: 50%;
        -webkit-transform: translateX(-50%);
                transform: translateX(-50%);
        box-shadow: unset;
    }

    /* ***************** Section 2 children ************* */
    .section2 {
        padding: 300px 30px 0 30px;
        height: 120vh;
    }

    .left_side {
        font-size: 40px;
    }

    .call_to_action {
        font-size: 25px;
    }

    /* ***************** Section 3 children ************* */
    .contact_section {
        margin: 0 10px;
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
        font-size: 17px;
    }
}

@media only screen and (min-width: 1440px) {

    /* ***************** Section 1 children ************* */
    .section1 {
        height: 900px;
    }

    .section1 .greeting {
        font-size: 200px;
    }

    /* ***************** Section 2 children ************* */
    .section2 {
        height: 1800px
    }

    .left_side {
        font-size: 180px;
    }

    .call_to_action {
        font-size: 120px;
    }

    /* ***************** Section 3 children ************* */
    .contact_section {
        width: 100%;
        font-size: 40px;
    }
}

@media only screen and (min-width: 1920px) {

    /* ***************** Section 1 children ************* */
    .section1 {
        height: 1200px;
    }

    .section1 .greeting {
        font-size: 235px;
    }

    /* ***************** Section 2 children ************* */
    .section2 {
        height: 2400px;
    }

    .left_side {
        font-size: 220px;
    }

    .call_to_action {
        font-size: 180px;
    }
}

@media only screen and (min-width: 2560px) {

    /* ***************** Section 1 children ************* */
    .section1 {
        height: 1200px;
    }

    .section1 .greeting {
        font-size: 295px;
    }

    /* ***************** Section 2 children ************* */
    .section2 {
        height: 2400px;
    }

    /* ***************** Section 3 children ************* */
    .contact_section {
        width: 100%;
        font-size: 80px;
    }
}
:root {
    --nav_bar_height: 80px;
}

.nav_bar {
    position: fixed;
    top: 0;
    z-index: 9999;
    transition-duration: 0.3s;
    pointer-events: none;
}

.nav_container {
    width: 100%;
    height: 100%;
    position: absolute;
    transition-duration: 0.3s;
    opacity: 0;
    background-color: rgba(82, 138, 39, 0.9);
}

.icon-container {
    display: flex;
    justify-content: center;
    align-items: center;

    color: white;
}

.menu-switch {
    width: 80px;
    width: var(--nav_bar_height);
    height: 80px;
    height: var(--nav_bar_height);
    position: absolute;
    right: 0%;
    z-index: 999999;

    font-size: 4em;
    pointer-events: all;
}

.menu-switch i {
    cursor: pointer;
}

.menu-items {
    margin: 0;
    padding: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);

    display: flex;

    justify-content: center;
    align-items: center;
    text-align: center;

    list-style: none;

    color: white;
}

.menu-items li {
    font-size: 30px;
    margin-right: 6px;
}

.menu-items li a {
    text-decoration: none;
    color: white;
    pointer-events: all;
}

@media screen and (max-width: 600px) {
    .nav_bar {
        width: 100vw;
        height: 100vh;
    }

    .nav_container {
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
    }

    .menu-switch {
        font-size: 2em;
    }

    .logo {
        position: absolute;
        top: 50%;
        left: 50%;
        -webkit-transform: translate(-50%, -50%);
                transform: translate(-50%, -50%);

        font-size: 20em;
        opacity: 0.3;

        color: red;
    }

    .menu-items {
        flex-direction: column;
    }

    .menu-items li {
        font-size: 50px;
        margin-bottom: 6px;
    }
}

@media screen and (min-width: 600px) {
    .nav_bar {
        width: 100%;
        height: 80px;
        height: var(--nav_bar_height);
    }

    .nav_container {
        -webkit-transform: translateY(-100%);
                transform: translateY(-100%);
    }

    .logo {
        width: 80px;
        width: var(--nav_bar_height);
        height: 100%;
        position: absolute;
        left: 0%;

        font-size: 4em;
    }

    .menu-items {
        width: 50vw;
    }
}

.menu-active {
    opacity: 1;
    -webkit-transform: translate(0%, 0%);
            transform: translate(0%, 0%);
}
